<template>
  <div class="mt-5">
    <v-snackbar v-model="snackbar">
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="pink" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
    <v-row class="mx-auto">
      <v-col cols="12" sm="12" md="6" lg="6" order="3" order-sm="2">
        <v-card class="pa-5 text-center" height="450px">
          <h2 class>Contact Us:</h2>

          <div class="pt-15">
            <span class="d-block" title="Kinplus Technologies"
              >Kinplus Technologies</span
            >
            <span class="d-block" title="Adress"
              >2nd Floor, 68B Christore Building,</span
            >
            <span class="d-block" title="Adress"
              >Opp. Ekiti State University Teaching
              Hospital</span
            >
            <span class="d-block" title="Adress"
              >Similoluwa, Ado-Ekiti,</span
            >
            <span class="d-block" title="Location"
              >Ekiti State, Nigeria.</span
            >
            <span class="d-block">Email: info@kinplusgroup.com</span>
            <span title="Kinplus Technologies, contact line."
              >Phone: +2347069718643, +2348071572767</span
            >
          </div>
        </v-card>
      </v-col>

      <v-col cols="12" sm="12" md="6" lg="6" order="1">
        <v-card>
          <v-container>
            <v-form v-model="valid" v-if="showForm">
              <v-text-field
                label="Name"
                v-model="form.fullname"
                required
                :rules="nameRules"
                :counter="30"
                prepend-icon="mdi-account-circle"
              ></v-text-field>
              <v-text-field
                label="E-mail"
                v-model="form.email"
                :rules="emailRules"
                required
                prepend-icon="mdi-email"
              ></v-text-field>
              <v-text-field
                label="Phone Number"
                v-model="form.phoneno"
                :counter="11"
                :rules="phoneRules"
                hint="Enter only numbers"
                prepend-icon="mdi-cellphone"
                required
              ></v-text-field>
              <v-text-field
                label="Subject"
                v-model="form.subject"
                required
                prepend-icon="mdi-comment-text"
              ></v-text-field>
              <v-textarea
                rows="2"
                label="Message"
                v-model="form.msg"
                required
                prepend-icon="mdi-comment-text"
              ></v-textarea>
              <v-divider></v-divider>
              <v-card-actions>
                <v-btn to="/" dark>Cancel</v-btn>
                <v-spacer></v-spacer>
                <v-btn
                  :disabled="!valid"
                  color="blue"
                  @click="submitForm"
                  :loading="loading"
                  >Submit</v-btn
                >
              </v-card-actions>
            </v-form>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { apiClient } from "@/services";
export default {
  name: "Contact",
  data() {
    return {
      form: this.initializeForm(),
      snackbar: false,
      loading: false,
      text: "Message submitted successfully",
      valid: true,
      showForm: true,
      firstname: "",

      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 30) || "Name must be less than 30 letters",
      ],
      phonenumber: "",
      phoneRules: [(v) => (v && v.length <= 11) || "Number must be less 12 digit "],

      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
      ],
    };
  },
  methods: {
    submitForm() {
      this.loading = true;
      apiClient
      .post("/contacts", this.form)
      .then((res) => {
        if (res.data.ok) {
          this.loading = false;
          this.snackbar = true;
          this.form = this.initializeForm();
          this.showForm = false;
          this.$nextTick(()=> this.showForm = true)
        }
      });
    },
    initializeForm: () => ({
      id: "",
      fullname: "",
      email: "",
      phoneno: "",
      subject: "",
      msg: "",
    }),
  },
};
</script>
